const BoxColageno = () => (
  <GenericBox title="O colgeno">
    <Paragraph>
      O colgeno  um tipo de protena estrutural que pode ser encontrado em
      grandes quantidades no tecido conjuntivo, sintetizada pelos fibroblastos,
      sendo o principal constituinte da matriz extracelular de tendes, ossos,
      cartilagens, pele, e pode ser encontrado nos msculos e na camada da
      crnea dos olhos, por exemplo.  uma protena de origem animal, que
      desempenha funes importantes para o corpo, como a manuteno da
      integridade estrutural do tecido e ainda confere resistncia mecnica e
      elasticidade.
    </Paragraph>
    <Paragraph>
      Essa protena estrutural  formada por uma unidade monomrica conhecida
      como tropocolgeno, o qual  formado por trs cadeias polipeptdicas alfa,
      que se apresentam na forma helicoidal, ligadas umas s outras por meio de
      ligaes de hidrognio. A estabilidade da forma helicoidal  garantida por
      meio da hidroxilisina e da hidroxiprolina, as quais so formados por
      hidroxilao da prolina e lisina. Essa organizao estrutural confere uma
      alta resistncia a essa protena.
    </Paragraph>
    <Paragraph>
      A formao do colgeno ocorre principalmente durante o processo de
      regenerao de tecidos e durante o desenvolvimento embrionrio. Os
      fibroblastos secretam essa molcula na forma de pr-colgeno solvel, o
      qual  secretado dentro das vesculas e, em seguida,  secretado para a
      matriz extracelular, onde as estruturas globulares ligadas ao pr-colgeno
      (uma contendo nitrognio e a outra apresentando carbono terminais), so
      clivadas por C- e N-peptidases. Esse processo de clivagem forma o
      tropocolgeno, o qual vai formar as fibrilas colgenas Essas enzimas so
      essenciais para o incio do processo de fibrognese, uma vez que essas
      molculas globulares ocupam um grande espao em volta da molcula. As
      fibrilas de colgeno so compostas de agregados de molculas de colgeno
      ao longo de um eixo paralelo. O conjunto de fibrilas forma as fibras de
      colgeno, que se organizam em feixes.
    </Paragraph>
    <Paragraph>
      O colgeno no  uma protena nica, mas uma famlia formada por
      aproximadamente 29 protenas isoformas encontradas nos tecidos conjuntivos
      do corpo, as quais possuem caractersticas nicas e algumas possuem
      caractersticas inter-relacionadas. Dessas 29, nove so frequentemente
      disponveis: colgenos os tipos I, II, III, IV, V, VII, IX, XI e XII, os
      quais podem ser divididos em tipos fibrilares e no fibrilares.
    </Paragraph>
    <Paragraph>
      Os colgenos tipos I, II, III, V, X, XXIV e XXVII alinham-se em grandes
      fibrilas extracelulares, sendo considerados como fibrilares. O colgeno do
      tipo IV  organizado em um entrelaamento dentro das membranas basais, o
      do tipo VI forma microfibrilas distintas e o tipo VII forma fibrilas de
      ancoragem. Alm disso, existem colgenos associados a fibrilas, que so
      dos tipos IX, XII, XIV, XIX, XX e XXI. Os tipos fibrilares I, II e III
      formam fibrilas de colgeno tpicas e so os colgenos mais abundantes no
      corpo, podendo ser encontrados em pele, tendo, ossos, dentina,
      cartilagem, corpo vtreo, ncleo pulposo, pele, parede dos vasos, fibras
      reticulares.
    </Paragraph>
    <Box sx={{ ml: 2 }}>
      <BoxAnchor
        title="Relao entre a vitamina C e o colgeno"
        box={<BoxVitaminaC />}
      >
        A vitamina C, ou cido ascrbico,  essencial para a biossntese do
        colgeno. Para se aprofundar nesse fenmeno, acesse este box
      </BoxAnchor>
    </Box>
  </GenericBox>
);

const BoxVitaminaC = () => (
  <GenericBox title="Relao entre a vitamina C e o colgeno">
    <Paragraph>
      A vitamina C  um tipo de vitamina hidrossolvel e termolbil. Com exceo
      dos primatas de ordem superior (incluindo o ser humano),
      porquinhos-da-ndia, algumas espcies de morcegos, peixes e pssaros,
      todas as espcies so capazes de produzir essa vitamina. Nas espcies
      incapazes de produzir essa vitamina, existe uma deficincia gentica da
      enzima gulonolactona oxidase, a qual  responsvel por catalisar a etapa
      final na biossntese de cido ascrbico, a partir da glicose. Dessa
      maneira, nas espcies supracitadas,  necessrio que haja um suplemento
      adequado dessa vitamina na dieta. Alm disso, por ser hidrossolvel, essa
      vitamina  facilmente excretada pelos organismos, no sendo armazenada no
      corpo, por isso  importante que haja uma administrao diria dessa
      vitamina, visto que suas reservas podem ser facilmente esgotadas.
    </Paragraph>
    <Paragraph>
      Essa vitamina pode ser encontrada na natureza sob duas formas: reduzida e
      oxidada. Essas duas formas so ativas igualmente, no entanto, a forma
      reduzida est mais difundida nas substncias naturais. Alm disso, podem
      ser de dois tipos, levgira (L) e dextrgira (D), no entanto, apenas a
      forma cido L-ascrbico  ativa.
    </Paragraph>
    <Paragraph>
      A vitamina C  absorvida quase totalmente pela mucosa do intestino delgado
      por transporte ativo, ou seja, h gasto de energia. Aps a sua absoro,
      essa vitamina passa para a corrente sangunea, sendo levada para todos as
      partes do corpo humano
    </Paragraph>
    <Paragraph>
      O cido ascrbico  importante para os seres humanos, uma vez que
      desempenha um importante papel nos processos celulares de oxirreduo, uma
      vez que essa molcula  responsvel por doar um eltron para um substrato,
      ou seja, atua como redutor ao mesmo tempo em que se oxida a um radical
      ascorbila, um radical livre que possui certa estabilidade. Duas molculas
      de ascorbila podem sofrer dismutao (processo qumico no qual uma parte
      das molculas sofre processo de oxidao e a outra de reduo) e formar 1
      molcula de ascorbato e 1 molcula de cido dehidroascrbico, que so as
      formas totalmente reduzida e oxidada da vitamina C, respectivamente. A
      reao de transformao do cido ascrbico em cido dehidroascrbico 
      considerada como reversvel, o que funciona como um sistema oxirredutor
      capaz de transportar hidrognio nos processos de respirao, a nvel
      celular.{" "}
    </Paragraph>
    <Paragraph>
      Alm de seu potencial oxirredutor, esse cido  importante para a sntese
      das catecolaminas, previne o escorbuto,  importante para na defesa do
      organismo contra infeces e  fundamental para a manter as paredes dos
      vasos sanguneos ntegras.
    </Paragraph>
    <Paragraph>
      No entanto, qual a sua relao com a formao do colgeno?
    </Paragraph>
    <Paragraph>
      Essa vitamina  de extrema importncia para o funcionamento das clulas do
      tecido conjuntivo responsvel pela formao do colgeno, os fibroblastos.
      O cido ascrbico funciona como um importante co-fator das enzimas prolil
      e lisil hidroxilase, que so essenciais para a biossntese do colgeno,
      uma vez que catalisam a hidroxilao dos resduos prolina e lisina nos
      polipeptdeos colgenos.{" "}
    </Paragraph>
    <Paragraph>
      Essa reao de hidroxilao  fundamental para que haja a formao e a
      estabilizao do colgeno de tripla-hlice, e sua secreo no espao
      extracelular, na forma de pr-colgeno, o qual  transformado
      subsequentemente em tropocolgeno, e, por fim, so formadas fibras
      colgenas por um rearranjo espacial espontneo dos tropocolgenos.
    </Paragraph>
    <Paragraph>
      Esse cido, atuando como co-fator das enzimas supracitadas, impede que
      haja a oxidao do ferro, o que iria causar a inativao desses
      catalisadores, promovendo, dessa maneira, a formao de uma rede colgena
      madura e normal por meio da manuteno da atividade enzimtica. Alm
      disso, essa molcula est envolvida na produo de colgeno do tipo I e
      III.
    </Paragraph>
    <Paragraph>
      Por fim, foi demonstrado que, at mesmo em indivduos em idade avanada,
      por volta dos 80 anos, o cido ascrbico  capaz de aumentar a sntese do
      colgeno em nveis semelhantes aos de clulas de recm-nascidos.
    </Paragraph>
    <Paragraph>
      Sendo assim, a falta dessa vitamina causa, sobretudo, o escorbuto, que se
      apresenta com hemorragia nas gengivas, visto que o colgeno  responsvel
      por dar a integridade dessas estruturas.
    </Paragraph>
  </GenericBox>
);

const BoxEhlerDanlos = () => (
  <GenericBox title="Ehlers Danlos">
    <Paragraph>
      A sndrome de Ehlers Danlos  uma doena de ordem gentica rara que
      acomete o tecido conjuntivo. Os pacientes portadores dessa sndrome
      apresentam uma hiperextensibilidade da pele, cicatrizao anormal de
      feridas e hipermobilidade articular.{" "}
    </Paragraph>
    <Paragraph>
      Essa sndrome  um conjunto de seis subtipos, os quais apresentam sintomas
      caractersticos para cada subtipo:
    </Paragraph>
    <Paragraph>
       Tipo 1 e Tipo 2:  a forma clssica da doena, sendo caracterizada pela
      hiperextensibilidade da pele, hematomas fceis, friabilidade tecidual,
      hipermobilidade articular.  de herana autossmica dominante;
    </Paragraph>
    <Paragraph>
       Tipo 3: tambm conhecido como hipermobilidade. Os pacientes apresentam
      hipermobilidade articular generalizada, luxao/dor articular recorrente,
      manifestaes cutneas leves. Herana autossmica dominante;
    </Paragraph>
    <Paragraph>
       Tipo 4: conhecido como o tipo vascular da doena, sendo uma herana
      autossmica dominante, sendo caracterizado por dismorfismo facial, pele
      translcida fina, m cicatrizao de feridas, hematomas, ruptura vascular
      e perfurao intestinal;
    </Paragraph>
    <Paragraph>
       Tipo 6:  chamado de cifoescolitico, apresentando pele macia e
      aveludada, m cicatrizao, frouxido articular, luxaes e cifose,
      fragilidade ocular, ruptura vascular, prolapso da vlvula mitral.  e
      herana autossmica recessiva;
    </Paragraph>
    <Paragraph>
       Tipo 7A-B: conhecido como o tipo artrocalsia, cujos pacientes
      apresentam hipermobilidade e luxaes articulares generalizadas graves,
      lactentes flcidos, escoliose, caractersticas cutneas leves.  um tipo
      de herana autossmica dominante;
    </Paragraph>
    <Paragraph>
       Tipo 7C: chamado de dermatoparaxis. Os pacientes acometidos por esse
      tipo vo apresentar manifestaes cutneas graves, com fragilidade,
      hematomas, m cicatrizao, ruptura visceral.  um tipo de herana
      autossmica dominante;
    </Paragraph>
    <Paragraph>
      O diagnstico dessa sndrome  feito por meio de um exame clnico e a
      partir do histrico familiar. Para o diagnstico clnico,  preciso seguir
      os critrios de Brighton, os quais podem ser divididos em principais e
      menores.
    </Paragraph>
    <TableContainer component={Paper} sx={{ m: 2 }}>
      <Table size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell align="center">
              Critrios de Beighton para diagnstico de Ehlers Danlos
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow key={0}>
            <TableCell component="th" scope="row">
              Critrios maiores
            </TableCell>
            <Stack>
              <TableCell component="th" scope="row">
                Pontuao de Beighton 4, agora ou no passado;
              </TableCell>
              <TableCell component="th" scope="row">
                Dor nas articulaes em 4 ou mais articulaes por 3 meses ou
                mais;
              </TableCell>
            </Stack>
          </TableRow>
          <TableRow key={1}>
            <TableCell component="th" scope="row">
              Critrios menores
            </TableCell>
            <Stack>
              <TableCell component="th" scope="row">
                Pontuao de Beighton 1-3 (ou 0-3 se tiver mais de 50 anos);
              </TableCell>
              <TableCell component="th" scope="row">
                Dor nas articulaes por mais de 3 meses em uma a trs
                articulaes;
              </TableCell>
              <TableCell component="th" scope="row">
                Dor nas costas, espondilose ou espondilolistese por 3 meses ou
                mais;
              </TableCell>
              <TableCell component="th" scope="row">
                Deslocamento em mais de uma articulao ou a mesma articulao
                mais de uma vez;
              </TableCell>
              <TableCell component="th" scope="row">
                Apresentar 3 ou mais leses em tecidos moles, como tenossinovite
                ou bursite;
              </TableCell>
              <TableCell component="th" scope="row">
                Pele anormal, como pele fina/elstica
              </TableCell>
              <TableCell component="th" scope="row">
                Sintomas oculares, como plpebras cadas ou miopia
              </TableCell>
              <TableCell component="th" scope="row">
                Hbito marfanide
              </TableCell>
              <TableCell component="th" scope="row">
                Varizes
              </TableCell>
              <TableCell component="th" scope="row">
                Hrnia
              </TableCell>
              <TableCell component="th" scope="row">
                Prolapso retal ou uterino
              </TableCell>
            </Stack>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
    <Paragraph>
      A pontuao de Beighton  definida realizando os seguintes testes:
    </Paragraph>
    <Paragraph>
       Aposio passiva do polegar na face flexora do antebrao (um ponto para
      cada mo);
    </Paragraph>
    <Paragraph>
       Dorsiflexo passiva do quinto dedo {">"} 90 (um ponto para cada mo);
    </Paragraph>
    <Paragraph>
       Hiperextenso do cotovelo {">"} 10 (um ponto para cada mo);
    </Paragraph>
    <Paragraph>
       Hiperextenso dos joelhos {">"} 10 (um ponto para cada perna);
    </Paragraph>
    <Paragraph>
       Flexo do tronco para a frente com os joelhos estendidos e as palmas das
      mos apoiadas no cho;
    </Paragraph>
    <Paragraph>
      Dessa maneira, a sndrome de Ehlers Danlos pode ser diagnosticada caso o
      paciente apresente: dois critrios maiores, um critrio maior e dois
      critrios menores, quatro critrios menores, dois critrios menores e um
      parente prximo, como um dos pais, diagnosticado com a sndrome da
      hipermobilidade articular.
    </Paragraph>
    <Paragraph>
      Essa sndrome pode levar a manifestaes cutneas (nas quais, ocorre uma
      hiperextensibilidade da pele, a qual se estende facilmente e recua aps a
      liberao, se apresenta com um aspecto frgil. Alm disso, as feridas
      levam mais tempo para cicatrizar), manifestaes musculoesquelticas
      (subluxao habitual e luxao das articulaes, instabilidade articular,
      deformidades do p, disfuno da articulao temporomandibular,
      osteoartrite), manifestaes de fragilidade tecidual (hrnia repetitiva,
      como hrnia inguinal, umbilical). Alm dessas manifestaes, os pacientes
      acometidos podem apresentar contuses fceis, excesso de pele nas
      plpebras, algumas cicatrizes dilatadas na testa e no queixo, hipotonia
      muscular, que pode causar atraso no desenvolvimento motor. Alm disso,
      podem ocorrer prolapso da valva mitral, ruptura espontnea de grandes
      artrias, juntamente com aneurismas intracranianos. Mulheres grvidas
      portadoras dessa sndrome podem apresentar parto prematuro, luxao dos
      quadris ou do ombro do recm-nascido.
    </Paragraph>
    <Paragraph>
      No existe um tratamento para curar a Sndrome ou para corrigir as
      anomalias no tecido conjuntivo.
    </Paragraph>
    <Paragraph>
      Como essa Sndrome  herdada de modo autossmico dominante,  estimado que
      aproximadamente 50% dos indivduos afetados tenham herdado o gene mutante
      de um pai afetado e cerca de 50% dos indivduos afetados tenham uma
      mutao causadora da doena de novo. Se um dos pais for afetado, os seus
      filhos estaro em risco de adquirir a doena. Para isso,  necessrio
      fazer um aconselhamento gentico, que deve ser realizado antes da
      gravidez.
    </Paragraph>
  </GenericBox>
);
