const BoxTrombofilia = () => (
  <GenericBox title="Trombofilias">
    <Paragraph>
      A trombofilia se refere a uma condio na qual existe uma maior propenso
       formao de cogulos sanguneos no corpo. Essa inclinao pode ser
      resultado de modificaes genticas herdadas ou adquiridas ao longo da
      vida, que afetam o processo de coagulao ou dissoluo dos cogulos.
      Essas alteraes proporcionam um ambiente favorvel para a formao de
      cogulos no sangue, o que pode acarretar em complicaes srias, como a
      trombose.  crucial ter conhecimento dessa condio e buscar um tratamento
      adequado para diminuir o risco de ocorrncia de eventos trombticos.
    </Paragraph>
    <Paragraph>
      A trombofilia  de origem hereditria quando  notado a presena de uma
      anormalidade hereditria que predispe  ocluso vascular, no entanto, 
      necessrio que haja interao com outro componente, seja ele hereditrio
      ou adquirido, para que o evento trombtico ocorra. As trombofilias
      hereditrias so geralmente causadas por mudanas nos inibidores naturais
      da coagulao (antitrombina, protena C, protena S e resistncia 
      protena C ativada) ou por mutaes em fatores de coagulao especficos
      (como FV G1691A, conhecido como Fator V Leiden, e mutao G20210A da
      protrombina). As trombofilias hereditrias costumam se manifestar
      clinicamente como tromboembolismos venosos e possuem caractersticas
      especficas, incluindo a ocorrncia em pessoas jovens, recorrncia
      frequente, histrico familiar de eventos trombticos, trombose em locais
      atpicos e a gravidade desproporcional dos episdios trombticos em
      relao ao fator desencadeante.
    </Paragraph>
    <Paragraph>
      Em alguns casos, a trombofilia pode ser adquirida, como em neoplasias,
      sndrome antifosfolpide, imobilizao, uso de medicamentos, como terapia
      de reposio hormonal, anticoncepcionais orais e heparina. A trombofilia
      adquirida pode se apresentar clinicamente de vrias maneiras, como a
      trombose venosa profunda, embolia pulmonar, tromboflebite superficial e
      trombose arterial. Alm disso, complicaes durante a gravidez tambm
      podem ocorrer, aumentando o risco de pr-eclmpsia, restrio de
      crescimento fetal, aborto espontneo recorrente e complicaes na
      placenta. Os sintomas e complicaes podem variar de acordo com o tipo
      especfico de trombofilia adquirida e  de fundamental importncia buscar
      orientao mdica para um diagnstico preciso e tratamento adequado.
    </Paragraph>
    <Paragraph>
      Pacientes tromboflicos tendem a apresentar uma maior probabilidade a
      desenvolver episdios de tromboembolismo venoso. O tromboembolismo venoso
      ocorre quando h a formao de um cogulo sanguneo dentro de um vaso que
      transporta sangue venoso. Esse cogulo pode permanecer no local onde se
      formou ou se deslocar para outras reas do sistema vascular. Essa condio
      pode se manifestar como trombose venosa profunda ou tromboembolismo
      pulmonar, com a presena de cogulos nas veias profundas das extremidades
      inferiores e na circulao arterial dos pulmes.
    </Paragraph>
  </GenericBox>
);
